.dashboard-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-toolbar {
  position: sticky;
  top: 0;
  z-index: 2;
}

.app-title {
  font-size: 20px;
  font-weight: 600;
}

.spacer {
  flex: 1 1 auto;
}

.new-list-button {
  margin-right: 16px;
}

.user-button {
  padding: 4px;
}

.user-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.user-avatar-large {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
}

.user-name {
  font-weight: 600;
  margin: 0;
}

.user-email {
  font-size: 14px;
  opacity: 0.7;
  margin: 0;
}

.sidenav-container {
  flex: 1;
}

.sidenav {
  width: 280px;
  padding: 0;
}

.sidenav mat-nav-list {
  padding-top: 0;
}

.sidenav a.active {
  background-color: rgba(0, 0, 0, 0.04);
}

.stat-badge {
  background-color: #1976d2;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.main-content {
  background-color: #fafafa;
}

.content-container {
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.welcome-section {
  margin-bottom: 32px;
}

.page-title {
  font-size: 32px;
  font-weight: 600;
  margin: 0 0 8px 0;
  color: #1976d2;
}

.page-subtitle {
  font-size: 16px;
  opacity: 0.7;
  margin: 0;
}

.stats-info {
  display: flex;
  flex-wrap: nowrap;
  gap: 16px;
  margin-bottom: 32px;
  padding-bottom: 8px;
}

.stat-card {
  flex: 1 0 auto;
  min-width: 140px;
  border-radius: 12px;
  transition: min-width 0.3s ease;
}

.stat-content {
  display: flex;
  align-items: center;
  gap: 16px;
}

.stat-icon {
  font-size: 40px;
  width: 40px;
  height: 40px;
  background-color: #e3f2fd;
  color: #1976d2;
}

.stat-icon.to-do {
  background-color: #fff3e0;
  color: #f57c00;
}

.stat-icon.done {
  background-color: #e8f5e8;
  color: #4caf50;
}

.stat-icon.progress {
  background-color: #f3e5f5;
  color: #9c27b0;
}

.stat-content h3 {
  font-size: 28px;
  font-weight: 700;
  margin: 0;
}

.stat-content p {
  font-size: 14px;
  opacity: 0.7;
  margin: 0;
  white-space: nowrap;
}

.section-title {
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 16px 0;
  color: #333;
}

.lists-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.list-card {
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  cursor: pointer;
}

.list-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.list-image {
  height: 160px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.list-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.7));
  color: white;
  padding: 16px;
}

.list-title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

.list-comment {
  font-size: 14px;
  opacity: 0.7;
  margin: 0 0 12px 0;
  line-height: 1.4;
}

.list-stats {
  margin-bottom: 8px;
}

.list-stats mat-chip-set {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.list-stats mat-chip {
  font-size: 12px;
}

.no-lists {
  text-align: center;
  padding: 40px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.empty-icon {
  font-size: 64px;
  width: 64px;
  height: 64px;
  opacity: 0.3;
  margin-bottom: 16px;
}

.no-lists h3 {
  margin: 0 0 8px 0;
  font-size: 20px;
}

.no-lists p {
  margin: 0 0 24px 0;
  opacity: 0.7;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  gap: 16px;
}

@media (max-width: 768px) {
  .sidenav {
    width: 240px;
  }

  .content-container {
    padding: 16px;
  }

  .page-title {
    font-size: 24px;
  }

  .stats-info {
    flex-wrap: nowrap;
    gap: 8px;
  }

  .stat-card {
    flex: 1;
    min-width: 0;
  }

  .stat-content p {
    display: none;
  }

  .lists-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}
